import React from 'react';
import { graphql, Link } from 'gatsby';
import Helmet from 'react-helmet';

import IndexLayout from 'layouts';
import { Page } from 'components/layout/Page';
import { DocsWrapper } from 'components/docs/DocsWrapper';
import { Container } from 'components/layout/Container';
import { DocsHeader } from 'components/docs/DocsHeader';
import { MarkdownContent } from 'components/page/Markdown';
import styled from 'styled-components';
import { Heading, Paragraph } from 'components/foundations';
import { space, colors, breakpoints } from 'utils/variables';
import { DocsContribution } from 'components/docs/DocsContribution';
import { FooterWrapper, PALFooter } from 'components/layout/Footer';
import { BackToTopButton } from 'components/docs/BackToTopButton';

import { Slider } from 'components/page/indexPage/ResponsiveSlider';
import { OurServices } from 'components/page/indexPage/OurServices';
import { WhyPALPortals } from 'components/page/indexPage/WhyPALPortals';
import { BlogList } from 'components/page/blogs/index';
// import illustration1 from 'assets/images/tutorials/spot-hello-world.svg';
// import illustration2 from 'assets/images/tutorials/spot-cms.svg';
// import illustration3 from 'assets/images/tutorials/spot-nl-studio.svg';
// import illustration4 from 'assets/images/tutorials/spot-nl-studio-integration.svg';

import { SiteMetadata } from '../interfaces/gatsby';

interface Props {
  data: {
    site: {
      siteMetadata: SiteMetadata;
    };
  };
}

const IndexPage: React.FC<Props> = ({ data }) => {
  const { site } = data;
  const { siteMetadata } = site;
  const slides = [
    data.image1.childImageSharp.fluid,
    data.image2.childImageSharp.fluid,
    data.image3.childImageSharp.fluid,
    data.image4.childImageSharp.fluid,
    data.image5.childImageSharp.fluid
  ];
  return (
    <IndexLayout navHidden>
      <Page docsPage>
        <Helmet>
          <title>Home &middot; {data.site.siteMetadata.title}</title>
          <meta property="og:title" content={data.site.siteMetadata.title} />
          <meta name="description" content={data.site.siteMetadata.description} />
          <meta property="og:description" content={data.site.siteMetadata.description} />
          <meta property="og:locale" content="en_US" />
          <meta property="og:type" content="website" />
          <meta property="og:site_name" content={data.site.siteMetadata.title} />
          <meta name="twitter:card" content={data.site.siteMetadata.title} />
          <meta name="twitter:description" content={data.site.siteMetadata.description} />
          <meta name="twitter:title" content="Home - Cloud Solutions" />
          <meta property="og:url" content={data.site.siteMetadata.siteUrl} />
        </Helmet>
        <DocsWrapper>
          <Container>
            <MarkdownContent>
              {/*https://medium.com/better-programming/build-an-image-slider-with-react-es6-264368de68e4 */}
              <Slider slides={slides} />
            </MarkdownContent>
            <MarkdownContent>
              <OurServices />
            </MarkdownContent>
            <MarkdownContent>
              <WhyPALPortals />
            </MarkdownContent>
            <MarkdownContent>
              <BlogList blogs={data.allMarkdownRemark.edges} isLimited={true} />
            </MarkdownContent>
            {/* <DocsHeader title="Tutorial" subtitle="A list of tutorials for using Kata Platform." />
            <MarkdownContent>
              <TutorialCard to="/tutorial/hello-world">
                <TutorialCardImage>
                  <img src={illustration1} alt="" />
                </TutorialCardImage>
                <TutorialCardContent>
                  <Heading size={600} mb="sm">
                    Creating a Simple Hello World Chatbot
                  </Heading>
                  <Paragraph>
                    Building a chatbot on Kata | Platform is quick and easy, even with a lack of programming knowledge.
                    In this tutorial, we will learn how to create a simple bot that says &quot;Hello World&quot; when
                    you message it.
                  </Paragraph>
                </TutorialCardContent>
              </TutorialCard>
              <TutorialCard to="/tutorial/bot-studio">
                <TutorialCardImage>
                  <img src={illustration2} alt="" />
                </TutorialCardImage>
                <TutorialCardContent>
                  <Heading size={600} mb="sm">
                    Creating a Pizza Ordering Bot with LINE
                  </Heading>
                  <Paragraph>
                    In this tutorial, we will learn how to create a simple bot on LINE Messenger to order a pizza. We
                    will also learn how to train your bot with NL Studio.
                  </Paragraph>
                </TutorialCardContent>
              </TutorialCard>
              <TutorialCard to="/tutorial/nl-studio">
                <TutorialCardImage>
                  <img src={illustration3} alt="" />
                </TutorialCardImage>
                <TutorialCardContent>
                  <Heading size={600} mb="sm">
                    Enhancing Chatbot with NL Studio
                  </Heading>
                  <Paragraph>
                    In Bot studio tutorial, we learned how to make keyword-based chatbot to order pizza. However,
                    keyword-based chatbot is not so smart. In this tutorial we will learn how to enhance the chatbot
                    using NL Studio.
                  </Paragraph>
                </TutorialCardContent>
              </TutorialCard>
              <TutorialCard to="/tutorial/integration-chatbot-with-nl-studio">
                <TutorialCardImage>
                  <img src={illustration4} alt="" />
                </TutorialCardImage>
                <TutorialCardContent>
                  <Heading size={600} mb="sm">
                    Integrating with NL Studio
                  </Heading>
                  <Paragraph>
                    In this tutorial, we will integrate the pizza ordering bot we just created with NL Studio.
                  </Paragraph>
                </TutorialCardContent>
              </TutorialCard>
            </MarkdownContent>
            <DocsContribution />*/}
            <FooterWrapper>
              <PALFooter
                version={siteMetadata.version}
                siteLastUpdated={siteMetadata.siteLastUpdated}
                socials={siteMetadata.socials}
              />
            </FooterWrapper>
          </Container>
          <BackToTopButton href="#" />
        </DocsWrapper>
      </Page>
    </IndexLayout>
  );
};

export default IndexPage;

export const query = graphql`
  query IndexPageQuery {
    site {
      siteMetadata {
        title
        sidebarTitle
        sidebarSubtext
        siteLastUpdated
        description
        version
        siteUrl
        keywords
        author {
          name
          url
          email
        }
        socials {
          name
          imgpath
          url
        }
      }
    }
    image1: file(relativePath: { eq: "1.png" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    image2: file(relativePath: { eq: "9.jpg" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    image3: file(relativePath: { eq: "10.jpg" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    image4: file(relativePath: { eq: "14.jpg" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    image5: file(relativePath: { eq: "bg1.jpg" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "/blogs/" }, frontmatter: { isPublish: { eq: true } } }
      sort: { fields: [frontmatter___createdon], order: DESC }
      limit: 3
    ) {
      edges {
        node {
          fields {
            slug
            layout
          }
          frontmatter {
            title
            description
            category
            header_image {
              ... on File {
                childImageSharp {
                  fluid(maxWidth: 500, quality: 92) {
                    ...GatsbyImageSharpFluid
                  }
                }
                extension
                publicURL
              }
            }
            createdby
            createdon
            url
          }
          timeToRead
          excerpt
        }
      }
    }
  }
`;
