import styled from 'styled-components';
import { space, colors, breakpoints } from 'utils/variables';

const Card = styled('div')`
  flex: 1 1 300px;
  // margin: 20px;
  padding: 40px 40px 40px 40px; // ${space.lg}px 10px;
  color: inherit;
  // border: 0px solid ${colors.grey02};
  // -webkit-border-radius: 50% / 14%;
  border-radius: 5% / 5%;
  // text-align: center;
  line-height: 64px;
  -o-text-overflow: clip;
  text-overflow: clip;
  z-index: 1;

  &:hover,
  &:focus {
    color: inherit;
    text-decoration: none;
    background-color: ${colors.grey02};
  }

  @media (min-width: ${breakpoints.lg}px) {
    // margin-left: 20px;
    // margin-right: 20px;
    margin: 20px;
  }
`;

export default Card;
