import React from 'react';
import styled from 'styled-components';
import { Link } from 'gatsby';
import { space, colors, breakpoints } from 'utils/variables';
import { Heading, Paragraph } from 'components/foundations';
import Icon from '../../../foundations/icons/Icon';

// import BusinessInteligent from '../../../foundations/icons/BusinessInteligent';

import {
  Container,
  HeaderPara,
  Para,
  Card,
  CardHeader,
  CardImage,
  CardContent,
  CardSection
} from '../../../styles/index';

import partnership from 'assets/images/svg/partnership.svg';
import commitment from 'assets/images/svg/commitment.svg';
import innovation from 'assets/images/svg/innovation.svg';

const Container1 = styled(Container)`
  background: #f9f9fc;
  padding-top: 40px;
  margin-bottom: 40px;
`;

// const ServiceInfo = styled('div')`
//   margin-top: 75px;
//   margin-bottom: -90px;
//   display: flex;
//   flex-direction: row;
//   flex-wrap: wrap;
//   justify-content: space-between;
// `;

const WhyPALPortals = () => {
  return (
    <Container1>
      <HeaderPara>Why PALPortals</HeaderPara>
      <Para>
        We are full-scale integrated Information Technology and Software Company. <br></br>
        We offer an integrated portfolio of products, solutions and services since a decade with in-house development
        team.
      </Para>
      <CardSection>
        <Card>
          <CardImage width={'100px'}>
            <img src={partnership} alt="Partnership" />
          </CardImage>
          <CardContent>
            <CardHeader>Partnership</CardHeader>
            <Paragraph>
              Providing a real-time digital solutions with high responsiveness, high intelligence and high performace to
              maintain long-term partnership.
            </Paragraph>
          </CardContent>
        </Card>
        <Card>
          <CardImage width={'100px'}>
            <img src={commitment} alt="Commitment" />
          </CardImage>
          <CardContent>
            <CardHeader>Commitment</CardHeader>
            <Paragraph>
              We are committed to total customer satisfaction through providing consistently high quality products and
              services by adopting best practices.
            </Paragraph>
          </CardContent>
        </Card>
        <Card>
          <CardImage width={'100px'}>
            <img src={innovation} alt="Innovation" />
          </CardImage>
          <CardContent>
            <CardHeader>Innovative</CardHeader>
            <Paragraph>
              We don't need social approval to go forward, our innovative thoughts lead us to make our deliverable
              unique that add values beyond expectation.
            </Paragraph>
          </CardContent>
        </Card>
      </CardSection>
    </Container1>
  );
};

export default WhyPALPortals;
