import styled from 'styled-components';
import { breakpoints } from 'utils/variables';

const CardTechImage = styled('div')`
  flex: 1 1 auto;
  img {
    display: block;
    height: 100px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 100px;
  }

  @media (min-width: ${breakpoints.lg}px) {
    flex: 1 0 auto;
  }
`;

export default CardTechImage;
