import styled from 'styled-components';

const FluidImage = styled('img')`
  background-color: #b2eae8;
  padding: 16px;
  object-fit: contain;
  height: 150px;
  width: 100%;
  aspect-ratio: 159 / 92; /* Amounts close to a 184px height */
  margin: auto;

  @media (min-width: 768px) {
    flex-grow: 0;
    flex-shrink: 0;
    align-self: stretch;
    height: 100%;
    width: 30%;
  }
`;

export default FluidImage;
