import styled from 'styled-components';

const FluidCard = styled('div')`
  background-color: #b2eae8;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
  -moz-box-pack: justify;
  justify-content: space-between;
  box-shadow: rgba(12, 15, 20, 0.02) 0px 0px 0px 1px, rgba(12, 15, 20, 0.06) 0px 0px 1px 0px,
    rgba(12, 15, 20, 0.06) 0px 2px 2px 0px;
  max-width: 344px;
  margin: 24px auto;

  @media (min-width: 768px) {
    flex-direction: row;
    max-width: 80%;
  }
`;

export default FluidCard;
